@import "../../assets/sass/index";

.container-aboutMe {
  background-color: white;
}

.titleWrapper {
  display: flex;
  flex-direction: row;
}

.title {
  font-family: "Cinzel";
  font-style: normal;
  font-weight: 700;
  letter-spacing: 0.05em;

  background: linear-gradient(180deg, #dfcdef 0%, #d6e2f9 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

  font-size: 2.5rem;
  @include sm {
    font-size: 3rem;
  }
  @include md {
    font-size: 4.8rem;
  }
  @include lg {
    font-size: 6rem;
  }
}

.card {
  background: linear-gradient(
    108.66deg,
    #d6e2f9 2.26%,
    #e3d4e9 43.91%,
    #f4f6fa 96.42%
  );
  border-radius: 20px;
  width: 100%;
  padding: 1rem;
}

.imgSparkles {
  width: 9%;
  height: 9%;
  @include xxl {
    width: 4%;
    height: 4%;
  }
}
